// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import vuex from './vuex/index'
import App from './App'
import router from './router'
import axios from 'axios'

Vue.config.productionTip = false

Vue.prototype.$http = axios

Vue.prototype.route = (str, id, type, typeId) => {
	$('#app').scrollTop(0)
	router.push({name: str,params:{id: id}, query:{type:type, id: typeId}})
}

Vue.directive(

'check-all', {
		twoWay: true,
        params: ['checkData'],
        bind() {
/**
             * 如果所有的列表的checked属性都为true,则选中全选框,否则不选中全选框
             */
            Vue.$watch(this.params.checkData, (checkData) => {
				if (checkData.every((item) => item.checked)) {
					this.set(true);
				} else {
					this.set(false);
				}
            }, { deep: true });
        },
        update(checkAll) {
/**
             * 如果全选框被选中,则将列表的所有checked属性转为true,否则转为false
             */
            if (checkAll) {
					Vue[this.params.checkData].forEach((item) => {
                    item.checked = true;
                });
            } else {
					Vue[this.params.checkData].forEach((item) => {
                    item.checked = false;
                });
            }
        },
    },
)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  vuex,
  components: { App },
  template: '<App/>'
})



